import {AfterViewInit, Component} from '@angular/core';
import {ModalController} from '@ionic/angular';
import {Router} from '@angular/router';
import {RadarNode, RainNode} from 'raain-model';
import {NewInstancePage} from './modals/new-instance.page';
import {FidjStorageNode, ProfileService} from '../shared';
import {environment} from '../../environments/environment';

@Component({
    selector: 'app-home',
    templateUrl: 'home.page.html',
    styleUrls: ['home.page.scss'],
})
export class HomePage implements AfterViewInit {
    public refreshInProgress = false;
    public homeInfoAlreadyViewed: any = {};
    public sessionData: FidjStorageNode;
    public rains: RainNode[] = [];
    public radars: RadarNode[] = [];
    public data: any;
    public appVersion = environment.version;

    constructor(
        private router: Router,
        private modalController: ModalController,
        private profile: ProfileService
    ) {
        this.refresh();
    }

    ngAfterViewInit() {
        this.refresh();
    }

    async closeInfo(windowName: string) {
        this.homeInfoAlreadyViewed[windowName] = true;
        if (!this.sessionData || !this.sessionData.infos) {
            return;
        }

        this.sessionData.infos.homeInfoAlreadyViewed = this.homeInfoAlreadyViewed;
        await this.profile.storeAll();
    }

    async clickOnNotifications() {
        return this.router.navigate(['/events']);
    }

    async clickOnRain(rain: RainNode) {
        return this.router.navigate(['/rains', rain.id]);
    }

    async presentModal() {
        const modal = await this.modalController.create({
            component: NewInstancePage,
            componentProps: {value: 123},
        });
        await modal.present();

        const {data} = await modal.onDidDismiss();
    }

    async refresh(event?: any) {
        this.refreshInProgress = true;
        try {
            this.sessionData = await this.profile.refreshProfile();
            if (this.sessionData.infos.homeInfoAlreadyViewed) {
                this.homeInfoAlreadyViewed = this.sessionData.infos.homeInfoAlreadyViewed;
            }

            this.rains = await this.profile.getRains();
            this.radars = await this.profile.getLonelyRadars(this.rains);
        } catch (e) {
            console.error('refresh pb:', e);
        }
        if (event) {
            event.target.complete();
        }
        this.refreshInProgress = false;

        if (!this.profile.isLoggedIn()) {
            return this.profile.gotoLout();
        }
    }
}
